@import url(https://fonts.googleapis.com/css2?family=Ravi+Prakash&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Electrolize&display=swap);
*{background:#2b7986}a{color:white;text-decoration:none}a:hover h3{color:black}.App{min-height:100vh;font-family:Arial, Helvetica, sans-serif;text-align:center;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center}.App .title{font-size:4rem;color:#f0c60e;font-family:'Ravi Prakash', cursive;margin:0}.App .calculator{margin-bottom:20px;max-width:400px;width:50%;display:-webkit-flex;display:flex;-webkit-flex-wrap:wrap;flex-wrap:wrap}.App .calculator .screen{background:white;height:80px;display:-webkit-inline-flex;display:inline-flex;-webkit-justify-content:flex-end;justify-content:flex-end;-webkit-align-items:flex-end;align-items:flex-end;width:100%;font-size:2.5rem}.App .calculator .screen span{background:white;text-align:right;font-family:'Electrolize', sans-serif}.App .calculator .keypad{padding-top:10px;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:space-between;justify-content:space-between;background:#934ac4;color:#2b7986;width:100%}.App .calculator .keypad .keypad-row{width:100%;display:-webkit-inline-flex;display:inline-flex;-webkit-justify-content:flex-start;justify-content:flex-start;-webkit-align-items:center;align-items:center;margin-bottom:4px;background:#934ac4}.App .calculator .keypad .keypad-row :not(:last-child){margin-right:2px}.App .calculator .keypad .keypad-row .key{cursor:pointer;width:25%;font-size:2rem;font-family:'Ravi Prakash', cursive;font-weight:bolder;background:white;padding:15px 0;border-radius:0%}.App .calculator .keypad .keypad-row .key:hover{background:#f54141;color:white}.App .calculator .keypad .keypad-row .key.ac{width:75%;background:#f54141;color:black}.App .calculator .keypad .keypad-row .key.ac:hover{background:#f0c60e;color:#f54141}.App .calculator .keypad .keypad-row .key.equals{background:#f0c60e;color:black;width:50%}.App .calculator .keypad .keypad-row .key.equals:hover{color:white;background:#f54141}@media (max-width: 750px){.App .calculator{max-width:auto;width:100%}}

